"use strict";

var shopAccordion = document.querySelectorAll(".accordion__header--shop");
var faqAccordion = document.querySelectorAll(".accordion__header--faq");
shopAccordion.forEach(function (trigger) {
  return trigger.addEventListener("click", accordDrop);
});
faqAccordion.forEach(function (trigger) {
  return trigger.addEventListener("click", singleAccordDrop);
});

function accordDrop() {
  var cards = document.querySelectorAll(".accordion__card");
  var thisCard = this.parentNode;
  cards.forEach(function (card) {
    if (thisCard == card) {
      thisCard.classList.toggle("accord-drop");
      return;
    }
  });
}

function singleAccordDrop() {
  var cards = document.querySelectorAll(".accordion__card");
  var thisCard = this.parentNode;
  cards.forEach(function (card) {
    if (thisCard == card) {
      thisCard.classList.toggle("accord-drop");
      return;
    }

    card.classList.remove("accord-drop");
  });
}